import React from 'react';
// import ajao from './ajao.png'


const Card = ({ name, attribute, id }) => {
    return (
        <div className='tc bg-light-green dib br3 pa2 ma3 grow bw2 shadow-5'>
            <img src={id} alt='anatomy' width ='120' height='170' />
            <div>
                <h2 className='pa3'>{name}</h2>
                <p>{attribute}</p>
            </div>
        </div>
    );
}


export default Card;