import React from 'react';
import Card from './Card'

const CardList = ( {list} ) => {
    return (
    <div>
        {
        list.map((user, i) => {
            return (
                <Card 
                key={i} 
                id={list[i].id} 
                name={list[i].name} 
                attribute={list[i].attribute} />
            );
        })
        }
    </div>
    );
}


export default CardList;